  .navbar {

      display: flex;
      
      align-items: center;
      
      justify-content: space-between;
      
      padding: 20px;
      
      background-color: teal;
      
      color: #fff;
      
      }
      
      .nav-links a {
      
      color: #fff;
      
      }
      
      /* LOGO */
      
      .logo {
      color: #f6f6f6;
      font-size: 32px;
      
      }
      
      /* NAVBAR MENU */
      
      .menu {
      
      display: flex;
      
      gap: 1em;
      
      font-size: 18px;
      
      }
      
      .menu li:hover {
      
      background-color: #4c9e9e;
      
      border-radius: 5px;
      
      transition: 0.3s ease;
      
      }
      
      .menu li {
      
      padding: 5px 14px;
      
      }
      
      /* DROPDOWN MENU */
      
      .services {
      
      position: relative; 
      
      }
      
      .dropdown {
      
      background-color: rgb(1, 139, 139);
      
      padding: 1em 0;
      
      position: absolute; /*WITH RESPECT TO PARENT*/
      
      display: none;
      
      border-radius: 8px;
      
      top: 35px;
      
      }
      
      .dropdown li + li {
      
      margin-top: 10px;
      
      }
      
      .dropdown li {
      
      padding: 0.5em 1em;
      
      width: 8em;
      
      text-align: center;
      
      }
      
      .dropdown li:hover {
      
      background-color: #4c9e9e;
      
      }
      
      .services:hover .dropdown {
      
      display: block;
      
      }



     








      body, div, form, input, select, textarea, label, p { 
        padding: 0;
        margin: 0;
        outline: none;
        font-family: Roboto, Arial, sans-serif;
        font-size: 14px;
        color: #666;
        line-height: 22px;
        }
        
        textarea {
        width: calc(100% - 12px);
        padding: 5px;
        }
        .testbox {
        display: flex;
        justify-content: center;
        align-items: center;
        height: inherit;
        padding: 20px;
        }
        form {
        width: 100%;
        padding: 20px;
        border-radius: 6px;
        background: #fff;
        box-shadow: 0 0 8px  #669999; 
        }
        .banner {
        position: relative;
        height: 300px;
        /* background-image: url("/uploads/media/default/0001/02/c1504011491c4e04e5158b63a27a4ea654b03ed1.jpeg");   */
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        }
        .banner::after {
        content: "";
        background-color: rgba(0, 0, 0, 0.2); 
        position: absolute;
        width: 100%;
        height: 100%;
        }
        input, select, textarea {
        margin-bottom: 10px;
        border: 1px solid #ccc;
        border-radius: 3px;
        }
        input {
        width: calc(100% - 10px);
        padding: 5px;
        }
        input[type="date"] {
        padding: 4px 5px;
        }
        textarea {
        width: calc(100% - 12px);
        padding: 5px;
        }
        .item:hover p, .item:hover i, .question:hover p, .question label:hover, input:hover::placeholder {
        color:  #669999;
        }
        .item input:hover, .item select:hover, .item textarea:hover {
        border: 1px solid transparent;
        box-shadow: 0 0 3px 0  #669999;
        color: #669999;
        }
        .item {
        position: relative;
        margin: 10px 0;
        }
        .item span {
        color: red;
        }

        .colums {
          display:flex;
          justify-content:space-between;
          flex-direction:row;
          flex-wrap:wrap;
          }
          .colums div {
          width:48%;
          }
          input[type="date"]::-webkit-inner-spin-button {
          display: none;
          }
          .item i, input[type="date"]::-webkit-calendar-picker-indicator {
          position: absolute;
          font-size: 20px;
          color:  #a3c2c2;
          }
          .item i {
          right: 1%;
          top: 30px;
          z-index: 1;
          }
          input[type=radio], input[type=checkbox]  {
          display: none;
          }
          label.radio {
          position: relative;
          display: inline-block;
          margin: 5px 20px 15px 0;
          cursor: pointer;
          }
          .question span {
          margin-left: 30px;
          }
          .question-answer label {
          display: block;
          }
          label.radio:before {
          content: "";
          position: absolute;
          left: 0;
          width: 17px;
          height: 17px;
          border-radius: 50%;
          border: 2px solid #ccc;
          }
          input[type=radio]:checked + label:before, label.radio:hover:before {
          border: 2px solid  #669999;
          }
          label.radio:after {
          content: "";
          position: absolute;
          top: 6px;
          left: 5px;
          width: 8px;
          height: 4px;
          border: 3px solid  #669999;
          border-top: none;
          border-right: none;
          transform: rotate(-45deg);
          opacity: 0;
          }
          input[type=radio]:checked + label:after {
          opacity: 1;
          }
          .flax {
          display:flex;
          justify-content:space-around;
          }
          .btn-block {
          margin-top: 10px;
          text-align: center;
          }
          button {
          width: 150px;
          padding: 10px;
          border: none;
          border-radius: 5px; 
          background:  #669999;
          font-size: 16px;
          color: #fff;
          cursor: pointer;
          }
          button:hover {
          background:  #a3c2c2;
          }
          @media (min-width: 568px) {
          .name-item, .city-item {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          }
          .name-item input, .name-item div {
          width: calc(50% - 20px);
          }
          .name-item div input {
          width:97%;}
          .name-item div label {
          display:block;
          padding-bottom:5px;
          }
          }


          .row {
            background: #f6f6f6;
            border: 1px solid #ccc;
            overflow: hidden;
            padding: 10px;
        }
        .col {
            float: left;
            width: 50%
        }







        .card_container{
          display: flex;
          justify-content: space-evenly;
          margin-top: 20px;
      }
      .icons{
           
          display: flex;
          justify-content: center;
          align-items: center;
      }
      .icons p{
          text-decoration: 3px solid blue underline;
          font-weight: 700;
      }
      .access_time{
          float: left;
      }
      .people_icon{
          margin-left: 30px;
      }










      footer {
        background-color: #f2f2f2;
        padding: 20px;
        text-align: center;
        font-size: 14px;
        color: #666;
      }
      
      footer p {
        margin: 0;
      }
      
      
      
      